import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { getProducts, editOrder } from '../../lib/Api';

import './OrderEdit.css';

export default class OrderEdit extends React.Component {
    constructor(props) {
        super(props);
        this.editOrder = this.editOrder.bind(this);
        this.handleQuantity = this.handleQuantity.bind(this);
        this.handleCode = this.handleCode.bind(this);
        this.initOrderDetails = this.initOrderDetails.bind(this);
        this.handleStatus = this.handleStatus.bind(this);
        this.state = {
            products: [],
            orderDetails: [],
            status: ''
        }
    }

    componentDidMount() {
        getProducts().then(res => {
            if (res.data != null && res.data.products.length >= 1) {
                this.setState({ products: res.data.products });
            }
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    editOrder() {
        let editedOrderDetails = [];
        if (this.state.orderDetails.length === 0) {
            for (let i = 0; i < this.props.order.orderDetails.length; i++) {
                editedOrderDetails[i] = { quantity: this.props.order.orderDetails[i].quantity, code: this.props.order.orderDetails[i].code }
            }
        } else {
            editedOrderDetails = this.state.orderDetails;
            for (let i = 0; i < editedOrderDetails.length; i++) {
                if (editedOrderDetails[i].quantity === '' || editedOrderDetails[i].quantity == null) {
                    editedOrderDetails[i].quantity = this.props.order.orderDetails[i].quantity;
                }
                if (editedOrderDetails[i].code === '' || editedOrderDetails[i].code == null) {
                    editedOrderDetails[i].code = this.props.order.orderDetails[i].code;
                }
            }
        }
        
        let editOrderBody = {
            orderDetails: editedOrderDetails,
            status: this.state.status === ''? this.props.order.status : this.state.status,
            orderId: this.props.order._id
        }
        editOrder(editOrderBody).then(res => {
            editOrderBody['price'] = res.data.order.price;
            this.props.updateOrder(editOrderBody);
            this.props.closeModal();
            this.setState({
                orderDetails: [],
                status: ''
            });
        }, err => {
            console.log(err);
        })
    }

    initOrderDetails(index, event, id) {
        let ordersSize = this.props.order.orderDetails.length;
        let newOrderDetails = []
        for (let i = 0; i < ordersSize; i++) {
            newOrderDetails[i] = { quantity: this.props.order.orderDetails[i].quantity, code: this.props.order.orderDetails[i].code }
        };
        if (id === 'quantity') {
            let newQuantity = Number(event.target.value);
            this.setState(prevState => ({
                orderDetails: newOrderDetails.map((orderDetail, pointer) => {
                    return pointer === index? {...orderDetail, quantity: newQuantity} : orderDetail;
                })
            }));
        } else {
            let newCode = event.target.value;
            this.setState(prevState => ({
                orderDetails: newOrderDetails.map((orderDetail, pointer) => {
                    return pointer === index? {...orderDetail, code: newCode} : orderDetail;
                })
            }));
        }
    }

    handleQuantity(i, event) {
        if (this.state.orderDetails.length === 0) {
            this.initOrderDetails(i, event, 'quantity');
        } else {
            this.handleQuantityHelper(i, event.target.value);
        }
    }

    handleQuantityHelper(i, newQuantity) {
        this.setState(prevState => ({
            orderDetails: prevState.orderDetails.map((orderDetail, index) => {
                return index === i? { ...orderDetail, quantity: Number(newQuantity) } : orderDetail;
            })
        }));
    }

    handleCode(i, event) {
        if (this.state.orderDetails.length === 0) {
            this.initOrderDetails(i, event, 'code');
        } else {
            this.handleCodeHelper(i, event.target.value);
        }
    }

    handleCodeHelper(i, newCode) {
        this.setState(prevState => ({
            orderDetails: prevState.orderDetails.map((orderDetail, index) => {
                return index === i? { ...orderDetail, code: newCode } : orderDetail;
            })
        }));
    }

    handleStatus(event) {
        this.setState({ status: event.target.value });
    }

    render() {
        if (this.props.order.orderDetails) {
            return(
                <div>
                    { (this.props.showModal) ? 
                        <Modal isOpen={this.props.showModal}>
                            <ModalHeader>Existing Order</ModalHeader>
                            <ModalBody>
                                <form>
                                    {
                                        this.props.order.orderDetails.map((orderDetail, index) => {
                                            return(
                                                <div className="row" key={index}>
                                                    <div className="col-md-3">Sub-order { index + 1 }:</div>
                                                    <div className="form-group col-md-4">
                                                        <select className="custom-select" value={this.state.orderDetails[index]? this.state.orderDetails[index].quantity : orderDetail.quantity} onChange={(event) => this.handleQuantity(index, event)}>
                                                            <option>1</option>
                                                            <option>2</option>
                                                            <option>3</option>
                                                            <option>4</option>
                                                            <option>5</option>
                                                        </select>
                                                    </div>
                                                    <div className="form-group col-md-4">
                                                        <select className="custom-select" value={this.state.orderDetails[index]? this.state.orderDetails[index].code : orderDetail.code} onChange={(event) => this.handleCode(index, event)}>
                                                            {
                                                                this.state.products.map((product, i) => {
                                                                    return <option key={i}>{product.code}</option>
                                                                })
                                                            }
                                                        </select>
                                                    </div>
                                                </div>
                                            )
                                        })
                                    }
                                    <hr />
                                    <div className="row">
                                        <div className="col-md-3">Status:</div>
                                        <div className="col-md-4 form-group">
                                            <select className="custom-select" value={this.state.status === ''? this.props.order.status : this.state.status} onChange={(event) => this.handleStatus(event)}>
                                                <option value="pending">pending</option>
                                                <option value="done">done</option>
                                            </select>
                                        </div>
                                    </div>
                                </form>
                            </ModalBody>
                            <ModalFooter>
                                <button type="submit" className="btn btn-info" onClick={this.editOrder}>Edit</button>
                                <button className="btn btn-danger" onClick={(event) => this.props.closeModal(event)}>Cancel</button>
                            </ModalFooter>
                        </Modal>
                    : null }
                </div>
            );
        } else {
            return null;
        }
       
    }
}