import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { getProducts, getUsers, insertOrder } from '../../lib/Api';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import Select from 'react-select';

import './OrderInsert.css';

export default class OrderInsert extends React.Component {
    constructor(props) {
        super(props);
        this.addOrderDetail = this.addOrderDetail.bind(this);
        this.removeSelect = this.removeSelect.bind(this);
        this.handleCode = this.handleCode.bind(this);
        this.handleQuantity = this.handleQuantity.bind(this);
        this.handleUser = this.handleUser.bind(this);
        this.getAllProducts = this.getAllProducts.bind(this);
        this.getAllUsers = this.getAllUsers.bind(this);
        this.filterUsernames = this.filterUsernames.bind(this);
        this.setFilterUsernames = this.setFilterUsernames.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.state = {
            users: [],
            products: [],
            order: {},
            selects: [],
            filterUsernames: [],
            selectedUser: null,
            errMsg: '',
            hideAlert: true
        }
    }

    componentDidMount() {
        this.getAllProducts();
        this.getAllUsers();
    }

    getAllUsers() {
        getUsers().then(res => {
            if (res.data != null && res.data.users.length >= 1) {
                this.setState({ users: res.data.users });
                this.setState({ selectedUser: res.data.users[0] })
                let usernames = [];
                for (let i = 0; i < res.data.users.length; i++) {
                    usernames[i] = {
                        label: res.data.users[i].instagramUsername,
                        value: res.data.users[i].instagramUsername
                    }
                }
                this.setFilterUsernames(usernames);
            }
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    setFilterUsernames(users) {
        this.setState({ filterUsernames: users });
    }

    getAllProducts() {
        getProducts().then(res => {
            if (res.data != null && res.data.products.length >= 1) {
                this.setState({ products: res.data.products, selects: [{ quantity: 1, code: res.data.products[0].code }] });
            }
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    addOrderDetail(event) {
        event.preventDefault();
        this.setState({ selects: [...this.state.selects, { quantity: 1, code: this.state.products[0].code }] });
    }

    removeSelect(event, index) {
        event.preventDefault();
        let newSelects = [...this.state.selects];
        newSelects.splice(index, 1);
        this.setState({ selects: newSelects });
    }

    handleQuantity(index, event) {
        let newQuantity = event.target.value;
        if (this.state.selects[index]) {
            this.setState(prevState => ({
                selects: prevState.selects.map((select, i) => {
                    return index === i? { ...select, quantity: Number(newQuantity) } : select;
                })
            }));
        } else {
            this.setState({ selects: [...this.state.selects, { quantity: Number(newQuantity) } ] });
        }
    }

    handleCode(index, event) {
        let newCode = event.target.value;
        if (this.state.selects[index]) {
            this.setState(prevState => ({
                selects: prevState.selects.map((select, i) => {
                    return index === i? { ...select, code: newCode } : select;
                })
            }));
        } else {
            this.setState({ selects: [...this.state.selects, { code: newCode } ] });
        }
    }

    handleUser(selectedOption) {
        this.setState({ order: { customerId: selectedOption.value }, selectedUser: selectedOption.value });
    }

    submitOrder() {
        let order = this.state.order;
        order['orderDetails'] = this.state.selects;
        if (order.customerId === '' || order.customerId == null) {
            this.setState({ errMsg: 'no Instagram username is supplied', hideAlert: false });
        } else {
            insertOrder(order).then(res => {
                this.setState({ errMsg: '', hideAlert: true });
                this.props.getOrders();
                this.props.closeModal();
            }, err => {
                this.setState({ errMsg: err.response.data.msg, hideAlert: false });
            });
        }
    }

    closeModal() {
        this.setState({ errMsg: '', hideAlert: true });
        this.props.closeModal();
    }

    filterUsernames(event) {
        const filterUsername = event.target.value;
        this.setState(prevState => ({
            filterUsernames: this.state.users.filter((user, i) => {
                return user.instagramUsername.includes(filterUsername);
            })
        }));
    }

    render() {
        let { selectedUser } = this.state;
        if (selectedUser !== null) {
            selectedUser = selectedUser.instagramUsernamel;
        }
        return(
            <div>
                <Modal isOpen={this.props.showModalInsert}>
                        <ModalHeader>New Order</ModalHeader>
                        <ModalBody>
                        <form>
                            <div className="form-group">
                                <div className="alert alert-danger" role="alert" hidden={this.state.hideAlert}>
                                    { this.state.errMsg }
                                </div>
                                <label>Instagram Username:</label>
                                <Select
                                    value={selectedUser}
                                    onChange={this.handleUser}
                                    options={this.state.filterUsernames}
                                />
                            </div>
                            <hr />
                            <div className="add-btn-container">
                                <button className="btn btn-info" onClick={ (event) => this.addOrderDetail(event) }>Add Suborder</button>
                            </div>

                            {
                                this.state.selects.map((select, index) => {
                                    return(
                                        <div className="row" key={ index }>
                                            <div className="col-md-3">Sub-order {index + 1}</div>
                                            <div className="col-md-3 form-group">
                                                <select className="custom-select" value={this.state.selects[index].quantity} onChange={(event) => this.handleQuantity(index, event)}>
                                                    <option>1</option>
                                                    <option>2</option>
                                                    <option>3</option>
                                                    <option>4</option>
                                                    <option>5</option>
                                                </select>
                                            </div>
                                            <div className="col-md-3 form-group">
                                                <select className="custom-select" value={this.state.selects[index].code} onChange={(event) => this.handleCode(index, event)}>
                                                    {
                                                        this.state.products.map((product, index) => {
                                                            return <option key={ index }>{ product.code }</option>
                                                        })
                                                    }
                                                </select>
                                            </div>
                                            <div className="col-md-3">
                                                <FontAwesomeIcon className="fa-times-delete" hidden={ this.state.selects.length === 1 } onClick={ (event) => this.removeSelect(event, index) } icon={faTimes} />
                                            </div>
                                        </div>
                                    )
                                })
                            }
                        </form>
                        </ModalBody>
                        <ModalFooter>
                            <button type="submit" className="btn btn-info" onClick={(event) => this.submitOrder(event)}>Add Order</button>
                            <button type="button" className="btn btn-danger" onClick={this.closeModal}>Cancel</button>
                        </ModalFooter>
                    </Modal>
            </div>
        )
    }
}