import React from 'react';
import OrderEdit from '../OrderEdit/OrderEdit';
import OrderInsert from '../OrderInsert/OrderInsert';
import Paginator from '../Paginatior/Paginatior';

import { getOrders, deleteOrders } from '../../lib/Api';
import { addDeleteCheckers } from '../../lib/helper_functions';

import './Order.css';

export default class Order extends React.Component {
    constructor(props) {
        super(props);
        this.changeCheckboxValue = this.changeCheckboxValue.bind(this);
        this.deleteOrders = this.deleteOrders.bind(this);
        this.filterOrders = this.filterOrders.bind(this);
        this.editOrder = this.editOrder.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.insertOrder = this.insertOrder.bind(this);
        this.getAllOrders = this.getAllOrders.bind(this);
        this.handleOrdersShown = this.handleOrdersShown.bind(this);
        this.updateOrder = this.updateOrder.bind(this);
        this.checkAllDelete = this.checkAllDelete.bind(this);
        this.setOrdersShown = this.setOrdersShown.bind(this);
        this.state = {
            orders: [],
            showModal: false,
            showModalInsert: false,
            orderToEdit: {},
            itemsPerPage: 3,
            activePage: 1,
            ordersShown: [],
            checkAll: false
        }
    }

    getAllOrders() {
        getOrders().then(res => {
            if (res.data != null && res.data.orders.length >= 1) {
                this.setState({ orders: addDeleteCheckers(res.data.orders) });
                this.setOrdersShown();
            }
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    componentDidMount() {
        this.getAllOrders();
    }

    setOrdersShown() {
        const indexOfLastThing = this.state.activePage * this.state.itemsPerPage;
        const indexOfFirstThing = indexOfLastThing - this.state.itemsPerPage;
        this.setState({ ordersShown: this.state.orders.slice(indexOfFirstThing, indexOfLastThing) });
    }

    filterOrders() {
        let orders = this.state.orders;
        let filteredOrders = orders.filter(order => {
            return !order['toDelete'];
        });
        const indexOfLastThing = this.state.activePage * this.state.itemsPerPage;
        const indexOfFirstThing = indexOfLastThing - this.state.itemsPerPage;
        this.setState({ orders: filteredOrders, ordersShown: filteredOrders.slice(indexOfFirstThing, indexOfLastThing) });
    }

    insertOrder() {
        this.setState({ showModalInsert: true });
    }

    deleteOrders() {
        const idsToDelete = [];
        this.state.orders.map((order) => {
            if (order['toDelete']) { 
                idsToDelete.push(order._id);
            }
        });
        for (let i = 0; i < idsToDelete.length; i++) {
            deleteOrders(idsToDelete[i]).then(res => {
                this.filterOrders();
            }).catch(err => {
                console.log(err);
            });
        }
    }

    editOrder(order) {
        this.setState({ orderToEdit: order, showModal: true});
    }

    updateOrder(order) {
        this.setState(prevState => ({
            orders: prevState.orders.map((stateOrder) => {
                return order.orderId === stateOrder._id? {...stateOrder, orderDetails: order.orderDetails, price: order.price, status: order.status} : stateOrder;
            })
        }), () => {
            this.setOrdersShown()
        });
    }

    changeCheckboxValue(index, event) {
        if (index === -1) {
            this.setState(prevState => ({
                orders: prevState.orders.map((stateOrder) => {
                    stateOrder['toDelete'] = prevState.checkAll? false : true;
                    return stateOrder;
                }), checkAll: !prevState.checkAll
            }));
            
            return;
        } else if (this.state.activePage > 1) {
            index += ((this.state.activePage - 1) * this.state.itemsPerPage);
        }
        let orders = this.state.orders;
        orders[index]['toDelete'] = event.target.checked;
        this.setState({ orders });
    }

    closeModal() {
        this.setState({ showModal: false, showModalInsert: false });
    }

    handleOrdersShown(orders, activePage) {
        this.setState({ ordersShown: orders, activePage });
    }

    checkAllDelete() {
        let orders = this.state.orders;
        for (let i = 0; i < orders; i++) {
            if (!orders[i]['toDelete']) {
                return false;
            }
        }
        return true;
    }

    render() {
        return(
            <div className="row orders__container">
                <div className="col-md-10 offset-md-1">
                    <div className="orders__btn-container">
                        <button className="btn btn-info orders__btn" onClick={this.insertOrder}>Insert</button>
                        <button className="btn btn-danger orders__btn" onClick={this.deleteOrders}>Delete</button>
                    </div>
                    <table className="table table-bordered">
                        <thead className="thead-light">
                            <tr>
                                <th>
                                    <div className="form-group">
                                        <div className="custom-control custom-checkbox">
                                            <input className="custom-control-input" id={ "customCheckAll" } type="checkbox" checked={this.state.checkAll} onChange={(event) => this.changeCheckboxValue(-1, event)} />
                                            <label className="custom-control-label" htmlFor={ "customCheckAll" }></label>
                                        </div>
                                    </div>
                                </th>
                                <th>Customer</th>
                                <th>Order</th>
                                <th>Price</th>
                                <th>Date</th>
                                <th>Status</th>
                                <th>Edit</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.ordersShown.map((order, index) => {
                                    return(
                                        <tr key={ index }>
                                            <td>
                                                <div className="form-group">
                                                    <div className="custom-control custom-checkbox">
                                                        <input className="custom-control-input" id={ "customCheck" + index } type="checkbox" checked={order['toDelete']} onChange={(event) => this.changeCheckboxValue(index, event)} />
                                                        <label className="custom-control-label" htmlFor={ "customCheck" + index }></label>
                                                    </div>
                                                </div>
                                            </td>
                                            <td>{ order.customerId }</td>
                                            <td>
                                                {
                                                    order.orderDetails.map((orderDetail, i) => {
                                                        return(
                                                            <div key={ i }>{ orderDetail.quantity } x { orderDetail.code }</div>
                                                        )
                                                    })
                                                }
                                            </td>
                                            <td>{ order.price }</td>
                                            <td>{ order.date }</td>
                                            <td>{ order.status }</td>
                                            <td>
                                                <button className="btn btn-info"  onClick={() => this.editOrder(order)}>Edit</button>
                                            </td>
                                        </tr>
                                    )
                                }) 
                            }
                        </tbody>
                    </table>
                    <Paginator items={this.state.orders} itemsPerPage={this.state.itemsPerPage} handleItems={this.handleOrdersShown} />
                    <OrderEdit showModal={this.state.showModal} order={this.state.orderToEdit} closeModal={this.closeModal} updateOrder={this.updateOrder}></OrderEdit>
                    <OrderInsert showModalInsert={this.state.showModalInsert} closeModal={this.closeModal} getOrders={this.getAllOrders}></OrderInsert>
                </div>
            </div>
        );
    }
}