import React from 'react';
import ProductInsert from '../ProductInsert/ProductInsert';
import Paginator from '../Paginatior/Paginatior';

import { getProducts, deleteProducts } from '../../lib/Api';
import { addDeleteCheckers } from '../../lib/helper_functions';
import './Product.css';

export default class Product extends React.Component {
    
    constructor(props) {
        super(props);
        this.changeCheckboxValue = this.changeCheckboxValue.bind(this);
        this.deleteProducts = this.deleteProducts.bind(this);
        this.filterProducts = this.filterProducts.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.insertProduct = this.insertProduct.bind(this);
        this.getAllProducts = this.getAllProducts.bind(this);
        this.handleProductsShown = this.handleProductsShown.bind(this);
        this.state = {
            products: [],
            showModal: false,
            showModalInsert: false,
            productsShown: [],
            itemsPerPage: 2,
            activePage: 1,
            checkAll: false
        }
    }

    componentDidMount() {
        this.getAllProducts();
    }

    insertProduct() {
        this.setState({ showModalInsert: true });
    }

    getAllProducts() {
        getProducts().then(res => {
            if (res.data != null && res.data.products.length >= 1) {
                this.setState({ products: addDeleteCheckers(res.data.products) });
                const indexOfLastThing = this.state.activePage * this.state.itemsPerPage;
                const indexOfFirstThing = indexOfLastThing - this.state.itemsPerPage;
                this.setState({ productsShown: this.state.products.slice(indexOfFirstThing, indexOfLastThing) });
            }
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    deleteProducts() {
        const idsToDelete = [];
        this.state.products.map((product) => {
            if (product['toDelete']) { 
                idsToDelete.push(product._id);
            }
        });
        for (let i = 0; i < idsToDelete.length; i++) {
            deleteProducts(idsToDelete[i]).then(res => {
                this.filterProducts();
            }).catch(err => {
                console.log(err);
            });
        }
    }

    filterProducts() {
        let products = this.state.products;
        let filteredProducts = products.filter(product => {
            return !product['toDelete'];
        });
        const indexOfLastThing = this.state.activePage * this.state.itemsPerPage;
        const indexOfFirstThing = indexOfLastThing - this.state.itemsPerPage;
        this.setState({ products: filteredProducts, productsShown: filteredProducts.slice(indexOfFirstThing, indexOfLastThing) });
    }

    changeCheckboxValue(index, event) {
        if (index === -1) {
            this.setState(prevState => ({
                products: prevState.products.map((stateProduct) => {
                    stateProduct['toDelete'] = prevState.checkAll? false : true;
                    return stateProduct;
                }), checkAll: !prevState.checkAll
            }));
            
            return;
        } else if (this.state.activePage > 1) {
            index += ((this.state.activePage - 1) * this.state.itemsPerPage);
        }
        let products = this.state.products;
        products[index]['toDelete'] = event.target.checked;
        this.setState({ products });
    }

    closeModal() {
        this.setState({ showModal: false, showModalInsert: false });
    }

    handleProductsShown(products, activePage) {
        this.setState({ productsShown: products, activePage });
    }

    render() {
        return(
            <div className="row products__container">
                <div className="col-md-10 offset-md-1">
                    <div className="products__btn-container">
                        <button className="btn btn-info products__btn" onClick={this.insertProduct}>Insert</button>
                        <button className="btn btn-danger products__btn" onClick={this.deleteProducts}>Delete</button>
                    </div>
                    
                    <div>
                        <table className="table table-bordered">
                            <thead className="thead-light">
                                <tr>
                                    <th>
                                        <div className="form-group">
                                            <div className="custom-control custom-checkbox">
                                                <input className="custom-control-input" id={ "customCheckAll" } type="checkbox" checked={this.state.checkAll} onChange={(event) => this.changeCheckboxValue(-1, event)} />
                                                <label className="custom-control-label" htmlFor={ "customCheckAll" }></label>
                                            </div>
                                        </div>
                                    </th>
                                    <th>Name</th>
                                    <th>Code</th>
                                    <th>Price</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.productsShown.map((product, index) => {
                                        return(
                                            <tr key={ index }>
                                                <td>
                                                    <div className="form-group">
                                                        <div className="custom-control custom-checkbox">
                                                            <input className="custom-control-input" id={ "customCheck" + index } type="checkbox" checked={product['toDelete']} onChange={(event) => this.changeCheckboxValue(index, event)} />
                                                            <label className="custom-control-label" htmlFor={ "customCheck" + index }></label>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td>{ product.name }</td>
                                                <td>{ product.code }</td>
                                                <td>{ product.price }</td>
                                            </tr>
                                        )
                                    }) 
                                }
                            </tbody>
                        </table>
                    </div>

                    <Paginator items={this.state.products} itemsPerPage={this.state.itemsPerPage} handleItems={this.handleProductsShown} />

                    <ProductInsert showModalInsert={this.state.showModalInsert} closeModal={this.closeModal} getProducts={this.getAllProducts}></ProductInsert>
                </div>
            </div>
        );
    }
}