import React from 'react';
import UserInsert from '../UserInsert/UserInsert';
import Paginator from '../Paginatior/Paginatior';

import { getUsers, deleteUsers } from '../../lib/Api';
import { addDeleteCheckers } from '../../lib/helper_functions';

import './User.css';

export default class User extends React.Component {

    constructor(props) {
        super(props);
        this.changeCheckboxValue = this.changeCheckboxValue.bind(this);
        this.deleteUsers = this.deleteUsers.bind(this);
        this.filterUsers = this.filterUsers.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.insertUser = this.insertUser.bind(this);
        this.getAllUsers = this.getAllUsers.bind(this);
        this.handleUsersShown = this.handleUsersShown.bind(this);
        this.state = {
            users: [],
            showModalInsert: false,
            itemsPerPage: 1,
            activePage: 1,
            usersShown: [],
            checkAll: false
        }
    }

    componentDidMount() {
        this.getAllUsers();
    }

    getAllUsers() {
        getUsers().then(res => {
            if (res.data != null && res.data.users.length >= 1) {
                this.setState({ users: addDeleteCheckers(res.data.users) });
                const indexOfLastThing = this.state.activePage * this.state.itemsPerPage;
                const indexOfFirstThing = indexOfLastThing - this.state.itemsPerPage;
                this.setState({ usersShown: this.state.users.slice(indexOfFirstThing, indexOfLastThing) });
            }
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    changeCheckboxValue(index, event) {
        if (index === -1) {
            this.setState(prevState => ({
                users: prevState.users.map((stateUser) => {
                    stateUser['toDelete'] = prevState.checkAll? false : true;
                    return stateUser;
                }), checkAll: !prevState.checkAll
            }));
            
            return;
        } else if (this.state.activePage > 1) {
            index += ((this.state.activePage - 1) * this.state.itemsPerPage);
        }
        let users = this.state.users;
        users[index]['toDelete'] = event.target.checked;
        this.setState({ users });
    }

    deleteUsers() {
        const idsToDelete = [];
        this.state.users.map((user) => {
            if (user['toDelete']) { 
                idsToDelete.push(user._id);
            }
        });
        for (let i = 0; i < idsToDelete.length; i++) {
            deleteUsers(idsToDelete[i]).then(res => {
                this.filterUsers();
            }).catch(err => {
                console.log(err);
            });
        }
    }

    filterUsers() {
        let users = this.state.users;
        let filteredUsers = users.filter(user => {
            return !user['toDelete'];
        });
        const indexOfLastThing = this.state.activePage * this.state.itemsPerPage;
        const indexOfFirstThing = indexOfLastThing - this.state.itemsPerPage;
        this.setState({ users: filteredUsers, usersShown: filteredUsers.slice(indexOfFirstThing, indexOfLastThing) });
    }

    insertUser() {
        this.setState({ showModalInsert: true });
    }

    closeModal() {
        this.setState({ showModalInsert: false });
    }

    handleUsersShown(users, activePage) {
        this.setState({ usersShown: users, activePage });
    }

    render() {
        return(
            <div className="row users__container">
                <div className="col-md-10 offset-md-1">
                    <div className="users__btn-container">
                        <button className="btn btn-info users__btn" onClick={this.insertUser}>Insert</button>
                        <button className="btn btn-danger users__btn" onClick={this.deleteUsers}>Delete</button>
                    </div>
                    <div>
                        <table className="table table-bordered">
                            <thead className="thead-light">
                                <tr>
                                    <th>
                                        <div className="form-group">
                                            <div className="custom-control custom-checkbox">
                                                <input className="custom-control-input" id={ "customCheckAll" } type="checkbox" checked={this.state.checkAll} onChange={(event) => this.changeCheckboxValue(-1, event)} />
                                                <label className="custom-control-label" htmlFor={ "customCheckAll" }></label>
                                            </div>
                                        </div>
                                    </th>
                                    <th>Name</th>
                                    <th>Instagram Username</th>
                                    <th>Email</th>
                                    <th>Phone</th>
                                    <th>Address</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.usersShown.map((user, index) => {
                                        return(
                                            <tr key={ index }>
                                                <td>
                                                    <div className="form-group">
                                                        <div className="custom-control custom-checkbox">
                                                            <input className="custom-control-input" id={ "customCheck" + index } type="checkbox" checked={user['toDelete']} onChange={(event) => this.changeCheckboxValue(index, event)} />
                                                            <label className="custom-control-label" htmlFor={ "customCheck" + index }></label>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td>{ user.name }</td>
                                                <td>{ user.instagramUsername }</td>
                                                <td>{ user.email }</td>
                                                <td>{ user.phone }</td>
                                                <td>{ user.address }</td>
                                            </tr>
                                        )
                                    }) 
                                }
                            </tbody>
                        </table>
                    </div>
                    <Paginator items={this.state.users} itemsPerPage={this.state.itemsPerPage} handleItems={this.handleUsersShown} />
                    <UserInsert showModalInsert={this.state.showModalInsert} closeModal={this.closeModal} getUsers={this.getAllUsers}></UserInsert>
                </div>
            </div>
        );
    }
}