import React from 'react';
import { authenticateAdmin } from '../../lib/Api';
import { Redirect } from 'react-router-dom';

import './Authentication.css';

export default class Authentication extends React.Component {
    constructor(props) {
        super(props);
        this.handleUsername = this.handleUsername.bind(this);
        this.handlePassword = this.handlePassword.bind(this);
        this.submitForm = this.submitForm.bind(this);
        this.state = {
            username: '',
            password: '',
            errMsg: '',
            hideAlert: true,
            redirectToDashboard: false
        };
    }

    handleUsername(event) {
        this.setState({ username: event.target.value });
    }

    handlePassword(event) {
        this.setState({ password: event.target.value });
    }

    submitForm(event) {
        event.preventDefault();
        if (this.state.username !== '' && this.state.password !== '') {
            let authBody = {
                username: this.state.username,
                password: this.state.password
            }
            authenticateAdmin(authBody).then(res => {
                localStorage.setItem('token', res.data);
                this.setState({ errMsg: '', hideAlert: true, redirectToDashboard: true });
            }).catch(err => {
                if (typeof err.response.data == 'object') {
                    this.setState({ errMsg: err.response.data.msg, hideAlert: false });
                } else {
                    this.setState({ errMsg: err.response.data, hideAlert: false });
                }
            });
        } else {
            this.setState({ errMsg: 'username and password can\'t be empty', hideAlert: false });
        }
    }

    render() {
        if (this.state.redirectToDashboard) {
            return <Redirect to="/dashboard" />
        }
        return(
            <div className="row auth__main-container">
                <div className="col-md-8 auth__left-container"></div>
                <div className="col-md-4 auth__right-container">
                    <div className="auth__login-form">
                        <form onSubmit={this.submitForm}>
                            <div className="alert alert-danger" role="alert" hidden={this.state.hideAlert}>
                                { this.state.errMsg }
                            </div>
                            <div className="auth__login-form-control">
                                <input type="text" className="form-control" placeholder="Username" onChange={this.handleUsername} autoComplete="new-password" />
                            </div>
                            <div className="auth__login-form-control">
                                <input type="password" className="form-control" placeholder="Password" onChange={this.handlePassword} autoComplete="new-password" />
                            </div>
                            <div className="auth__login-button">
                                <input type="submit" className="btn btn-info" value="Login" />
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}