import React from 'react';
import { NavLink } from 'react-router-dom';
import Header from '../Header/Header';

import './Dashboard.css';

export default class Dashboard extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            activeStyle: { backgroundColor: '#555', color: '#fff' },
            style: { backgroundColor: '#e9ecef' }
        }
    }

    render() {
        return(
            <div>
                <Header></Header>
                <div className="dashboard__container">
                    <div className="btn-group btn-group-lg" role="group" aria-label="Basic example">
                        <NavLink className="btn" to="/dashboard/orders" activeStyle={ this.state.activeStyle } style={ this.state.style }>
                            Orders
                        </NavLink>
                        <NavLink className="btn" to="/dashboard/products" activeStyle={this.state.activeStyle} style={ this.state.style }>
                            Products
                        </NavLink>
                        <NavLink className="btn" to="/dashboard/users" activeStyle={this.state.activeStyle} style={ this.state.style }>
                            Users
                        </NavLink>
                    </div>
                </div>
            </div>
        );
    }
}