import React from 'react';
import Pagination from "react-js-pagination";

import './Paginatior.css';

export default class Paginatior extends React.Component {

    constructor(props) {
        super(props);
        this.handlePageChange = this.handlePageChange.bind(this);
        this.state = {
            activePage: 1,
            itemsPerPage: this.props.itemsPerPage,
            items: this.props.items
        }
    }
    
    handlePageChange(pageNumber) {
        this.setState({ activePage: pageNumber }, () => {
            const indexOfLastThing = this.state.activePage * this.state.itemsPerPage;
            const indexOfFirstThing = indexOfLastThing - this.state.itemsPerPage;
            const itemsShown = this.props.items.slice(indexOfFirstThing, indexOfLastThing);
            this.props.handleItems(itemsShown, pageNumber);
        });
    }

    render() {
        return(
            <div className="pagination__container">
                <Pagination
                innerClass="pagination-overriden"
                onChange={this.handlePageChange}
                activePage={this.state.activePage}
                totalItemsCount={this.props.items.length}
                itemsCountPerPage={this.state.itemsPerPage}
                itemClass="item-page"
                activeClass="item-active-page"
                hideNavigation={true}
                linkClass="link-page"
                activeLinkClass="link-active-page"
                />
            </div>
        )
    }
}