import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { insertUser } from '../../lib/Api';

export default class UserInsert extends React.Component {
    constructor(props) {
        super(props);
        this.handleName = this.handleName.bind(this);
        this.handleInstagram = this.handleInstagram.bind(this);
        this.handleEmail = this.handleEmail.bind(this);
        this.handlePhone = this.handlePhone.bind(this);
        this.handleAddress = this.handleAddress.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.submitUser = this.submitUser.bind(this);
        this.state = {
            user: {},
            errMsg: '',
            hideAlert: true
        }
    }

    handleName(event) {
        const name = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                name   
            }
        }));
    }

    handleInstagram(event) {
        const instagramUsername = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                instagramUsername  
            }
        }));
    }

    handleEmail(event) {
        const email = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                email
            }
        }));
    }

    handlePhone(event) {
        const phone = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                phone
            }
        }));
    }

    handleAddress(event) {
        const address = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                address
            }
        }));
    }

    submitUser() {
        let user = this.state.user;
        insertUser(user).then(res => {
            this.setState({ errMsg: '', hideAlert: true });
            this.props.getUsers();
            this.props.closeModal();
        }, err => {
            if (typeof err.response.data == 'object') {
                this.setState({ errMsg: err.response.data.msg, hideAlert: false });
            } else {
                this.setState({ errMsg: err.response.data, hideAlert: false });
            }
        })
    }

    closeModal() {
        this.setState({ errMsg: '', hideAlert: true });
        this.props.closeModal();
    }

    render() {
        return(
            <div>
                <Modal isOpen={this.props.showModalInsert}>
                        <ModalHeader>New User</ModalHeader>
                        <ModalBody>
                        <div className="alert alert-danger" role="alert" hidden={this.state.hideAlert}>
                            { this.state.errMsg }
                        </div>
                        <form>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Name:</label>
                                    <input type="text" className="form-control" id="userName" placeholder="Enter Your Name" onChange={this.handleName} />
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Instagram Username:</label>
                                    <input type="text" className="form-control" id="userInstagram" placeholder="Enter Your Instagram Username" onChange={this.handleInstagram} />
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Email:</label>
                                    <input type="text" className="form-control" id="userEmail" placeholder="Enter Your Email" onChange={this.handleEmail} />
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Phone:</label>
                                    <input type="text" className="form-control" id="userPhone" placeholder="Enter Your Phone" onChange={this.handlePhone} />
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Address:</label>
                                    <input type="text" className="form-control" id="userAddress" placeholder="Enter Your Address" onChange={this.handleAddress} />
                                </div>
                            </div>
                        </form>
                        </ModalBody>
                        <ModalFooter>
                            <button type="submit" className="btn btn-info" onClick={(event) => this.submitUser(event)}>Add User</button>
                            <button type="button" className="btn btn-danger" onClick={this.closeModal}>Cancel</button>
                        </ModalFooter>
                    </Modal>
            </div>
        )
    }
}